$main-block-border-color: #e6e6e6;

html, body, #page{
	height:100%;
}

#header-wrapper{
	position:fixed;
	z-index:1;
	background:#f9f9f9;
	width:100%;
	top:0;
	height:55px;
	border-bottom:1px solid $main-block-border-color;
	box-shadow: 0 3px 8px #eee;
}

#main-wrapper{
	position:relative;
	top:55px;
}

#main, #header, #footer{
	position:relative;
	min-width: 980px;
	margin:0 auto;
}

#main{
	
}

#sidebar{
	position:fixed;
	width:240px;
	top:55px;
	height:100%;
	bottom:0;
	border-right:1px solid $main-block-border-color;
}

#content{
	position:relative;
	margin-left:240px;
	height:1000px;
}